@import '../../global.scss';

.careersContainer {

    header {
        height: 100vh;
        padding: 120px $page-padding-x-dl 0;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        background: url('../../assets/Careers/backgroundLogo.svg');
        background-repeat: no-repeat;
        background-position: 100% 50%;
        background-size: 100%;

        @media (min-width: 768px) {
            padding: 140px $page-padding-x-dl 0;
        }

        @media (min-width: $mobile) {
            padding: 120px $page-padding-x-m 0;
        }

        @media (min-width: $tablet-small) {
            padding: 120px $page-padding-x-ts 0;
        }

        @media (min-width: $tablet-large) {
            padding: 140px $page-padding-x-tl 0;
        }

        @media (min-width: $desktop-small) {
            padding: 140px $page-padding-x-ds 0;
        }

        @media (min-width: $desktop-large) {
            padding: 140px $page-padding-x-dl 0;
        }

        h1,
        h2 {
            font-family: 'Roboto';
            font-style: normal;
            font-weight: 500;
            font-size: 80px;
            line-height: 140%;
            /* or 112px */

            display: flex;
            align-items: center;
            letter-spacing: -0.015em;

            @media (min-width: $mobile) {
                font-size: 44px;
            }

            @media (min-width: $tablet-small) {
                font-size: 60px;
            }

            @media (min-width: $tablet-large) {
                font-size: 70px;
            }

            @media (min-width: $desktop-small) {
                font-size: 80px;
            }

            @media (min-width: $desktop-large) {
                font-size: 80px;
            }
        }

        p {
            margin-top: 40px;
            font-style: normal;
            font-weight: 400;
            font-size: 24px;
            line-height: 150%;
            /* or 36px */

            display: flex;
            align-items: center;
            letter-spacing: -0.015em;

            @media (min-width: $mobile) {
                font-size: 16px;
                width: initial;
            }

            @media (min-width: $tablet-small) {
                font-size: 24px;
                width: 620px;
            }

            @media (min-width: $tablet-large) {
                font-size: 24px;
                width: 700px;
            }

            @media (min-width: $desktop-small) {
                font-size: 24px;
                width: 800px;
            }

            @media (min-width: $desktop-large) {
                font-size: 24px;
                width: 800px;
            }
        }

        .headerP {
            display: block;

            @media (min-width: $mobile) {
                font-size: 16px;
                display: none;
            }

            @media (min-width: $tablet-small) {
                font-size: 24px;
                display: block;
            }

            @media (min-width: $tablet-large) {
                font-size: 24px;
                display: block;
            }

            @media (min-width: $desktop-small) {
                font-size: 24px;
                display: block;
            }

            @media (min-width: $desktop-large) {
                font-size: 24px;
                display: block;
            }
        }
    }

    .mobileDesc {
        display: none;

        @media (min-width: $mobile) {
            display: flex;
        }

        @media (min-width: $tablet-small) {
            display: none;
        }

        @media (min-width: $tablet-large) {
            display: none;
        }

        @media (min-width: $desktop-small) {
            display: none;
        }

        @media (min-width: $desktop-large) {
            display: none;
        }

        justify-content:center;
        align-items:center;
    }

    .banner {
        height: 100vh;
        background: url('../../assets/Careers/banner.jpg');
        background-size: cover;
        background-repeat: no-repeat;
        background-position: 35%;

        @media (max-width: 768px) {
            height: 60vh;
            min-height: 400px;
        }

        @media (min-width: 769px) {
            height: 100vh;
        }
    }

    .benefitsSection {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 100vh;
        flex-direction: row;
        padding: 0 $page-padding-x-dl;
        box-sizing: border-box;
        position: relative;
        overflow: visible;

        @media (max-width: 768px) {
            height: auto;
            flex-direction: column;
            padding: 60px $page-padding-x-m;
        }

        @media (min-width: 769px) {
            height: 100vh;
            flex-direction: row;
        }

        @media (min-width: $mobile) {
            height: initial;
            flex-direction: column;
            padding: 0 $page-padding-x-m;

            @media (max-height: 700px) {
                height: 200vh;
            }
        }

        @media (min-width: $tablet-small) {
            height: initial;
            flex-direction: column;
            padding: 0 $page-padding-x-ts;

            @media (max-height: 700px) {
                height: 200vh;
            }
        }

        @media (min-width: $tablet-large) {
            height: 100vh;
            flex-direction: row;
            padding: 0 $page-padding-x-tl;

            @media (max-height: 700px) {
                height: 200vh;
            }
        }

        @media (min-width: $desktop-small) {
            height: 100vh;
            flex-direction: row;
            padding: 0 $page-padding-x-ds;

            @media (max-height: 700px) {
                height: 200vh;
            }
        }

        @media (min-width: $desktop-large) {
            height: 100vh;
            flex-direction: row;
            padding: 0 $page-padding-x-dl;

            @media (max-height: 700px) {
                height: 200vh;
            }
        }

        .groupedImg {
            width: 45%;
            position: relative;
            margin-right: 4rem;
            overflow: visible;

            @media (min-width: $mobile) {
                width: 100%;
                margin-right: 0;
            }

            @media (min-width: $tablet-small) {
                width: 100%;
                margin-right: 0;
            }

            @media (min-width: $tablet-large) {
                width: 45%;
                margin-right: 4rem;
            }

            @media (min-width: $desktop-small) {
                width: 45%;
                margin-right: 4rem;
            }

            @media (min-width: $desktop-large) {
                width: 45%;
                margin-right: 4rem;
            }

            .gradientImg {
                display: none;
            }

            .circleImgWrapper {
                margin: 0;
                text-align: initial;
                position: relative;
                width: 100%;
                height: 500px;
                overflow: visible;

                @media (min-width: $mobile) {
                    text-align: center;
                    margin: 100px 0;
                    height: auto;
                }

                @media (min-width: $tablet-small) {
                    text-align: center;
                    margin: 100px 0;
                    height: auto;
                }

                @media (min-width: $tablet-large) {
                    text-align: initial;
                    margin: 0;
                    height: 500px;
                }

                @media (min-width: $desktop-small) {
                    text-align: initial;
                    margin: 0;
                    height: 500px;
                }

                @media (min-width: $desktop-large) {
                    text-align: initial;
                    margin: 0;
                    height: 500px;
                }

                .circleImg {
                    border-radius: 50%;
                    height: 500px;
                    width: 500px;
                    position: absolute;
                    left: 0;
                    top: 0;
                    object-fit: cover;

                    @media (min-width: $mobile) {
                        height: auto;
                        width: calc(100% - #{$page-padding-x-m * 2});
                        position: relative;
                    }

                    @media (min-width: $tablet-small) {
                        height: auto;
                        width: calc(100% - #{$page-padding-x-ts * 2});
                        position: relative;
                    }

                    @media (min-width: $tablet-large) {
                        height: 500px;
                        width: 500px;
                        position: absolute;
                    }

                    @media (min-width: $desktop-small) {
                        height: 500px;
                        width: 500px;
                        position: absolute;
                    }

                    @media (min-width: $desktop-large) {
                        height: 500px;
                        width: 500px;
                        position: absolute;
                    }
                }
            }
        }

        .benefits {
            width: 50%;
            display: flex;
            flex-direction: column;
            padding: 0;
            margin-top: 0;
            position: relative;
            z-index: 3;

            @media (min-width: $mobile) {
                width: 100%;
                margin-top: 24px;
                padding: 0;
            }

            @media (min-width: $tablet-small) {
                width: 100%;
                margin-top: 24px;
                padding: 0;
            }

            @media (min-width: $tablet-large) {
                width: 50%;
                margin-top: 0;
                padding: 0;
            }

            @media (min-width: $desktop-small) {
                width: 50%;
                margin-top: 0;
                padding: 0;
            }

            @media (min-width: $desktop-large) {
                width: 50%;
                margin-top: 0;
                padding: 0;
            }

            h3 {
                font-style: normal;
                font-weight: 500;
                font-size: 48px;
                margin-bottom: 50px;
                max-width: 100%;

                @media (min-width: $mobile) {
                    font-size: 26px;
                }

                @media (min-width: $tablet-small) {
                    font-size: 30px;
                }

                @media (min-width: $tablet-large) {
                    font-size: 36px;
                }

                @media (min-width: $desktop-small) {
                    font-size: 48px;
                }

                @media (min-width: $desktop-large) {
                    font-size: 48px;
                }
            }

            .benefitsList {
                display: flex;
                justify-content: space-between;
                flex-direction: row;
                gap: 2rem;

                @media (min-width: $mobile) {
                    flex-direction: column;
                    gap: 1rem;
                }

                @media (min-width: $tablet-small) {
                    flex-direction: row;
                    gap: 2rem;
                }

                @media (min-width: $tablet-large) {
                    flex-direction: row;
                    gap: 2rem;
                }

                @media (min-width: $desktop-small) {
                    flex-direction: row;
                    gap: 2rem;
                }

                @media (min-width: $desktop-large) {
                    flex-direction: row;
                    gap: 2rem;
                }

                .benefitsCol {
                    flex: 1;
                    display: flex;
                    flex-direction: column;

                    .benefit {
                        display: flex;
                        align-items: center;
                        padding: 22px 0;
                        border-bottom: 0.5px solid #CDCDCD;

                        .imgWrapper {
                            width: 24px;
                            height: 24px;
                            margin-right: 18px;
                            display: flex;
                            justify-self: center;
                            align-items: center;
                            flex-shrink: 0;

                            img {
                                height: 24px;
                                width: 24px;
                            }
                        }

                        p {
                            font-style: normal;
                            font-weight: 400;
                            font-size: 19px;
                            margin: 0;
                            line-height: 1.4;
                        }
                    }
                }
            }
        }
    }

    .rolesSection {
        width: 100%;
        padding: 0 $page-padding-x-dl;

        @media (min-width: $mobile) {
            padding: 150px $page-padding-x-m;
        }

        @media (min-width: $tablet-small) {
            padding: 150px $page-padding-x-ts;
        }

        @media (min-width: $tablet-large) {
            padding: 0 $page-padding-x-tl;
        }

        @media (min-width: $desktop-small) {
            padding: 0 $page-padding-x-ds;
        }

        @media (min-width: $desktop-large) {
            padding: 0 $page-padding-x-dl;
        }

        h3 {
            font-style: normal;
            font-weight: 500;
            font-size: 48px;
            width: 100%;
            border-bottom: 2px solid $grey;
            padding-bottom: 50px;

            @media (min-width: $mobile) {
                font-size: 26px;
            }

            @media (min-width: $tablet-small) {
                font-size: 30px;
            }

            @media (min-width: $tablet-large) {
                font-size: 36px;
            }

            @media (min-width: $desktop-small) {
                font-size: 48px;
            }

            @media (min-width: $desktop-large) {
                font-size: 48px;
            }
        }

        .rolesContainer {
            margin-top: 50px;

            .role {
                padding-top: 32px;
                padding-bottom: 16px;
                border-bottom: 1px solid #D6D6D6;
                display: flex;
                justify-content: space-between;

                .roleDesc {
                    h4 {
                        font-style: normal;
                        font-weight: 500;
                        font-size: 24px;
                        margin-bottom: 6px;

                        @media (min-width: $mobile) {
                            font-size: 16px;
                        }

                        @media (min-width: $tablet-small) {
                            font-size: 20px;
                        }

                        @media (min-width: $tablet-large) {
                            font-size: 24px;
                        }

                        @media (min-width: $desktop-small) {
                            font-size: 24px;
                        }

                        @media (min-width: $desktop-large) {
                            font-size: 24px;
                        }
                    }

                    p {
                        font-style: normal;
                        font-weight: 400;
                        font-size: 16px;
                        margin-bottom: 6px;

                        @media (min-width: $mobile) {
                            font-size: 12px;
                        }

                        @media (min-width: $tablet-small) {
                            font-size: 14px;
                        }

                        @media (min-width: $tablet-large) {
                            font-size: 16px;
                        }

                        @media (min-width: $desktop-small) {
                            font-size: 16px;
                        }

                        @media (min-width: $desktop-large) {
                            font-size: 16px;
                        }

                        color: #5A5A5A;
                    }
                }

                .apply {
                    a {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        text-decoration: none;
                        color: black;

                        p {
                            font-style: normal;
                            font-weight: 400;
                            font-size: 21px;
                            margin-right: 7px;

                            @media (min-width: $mobile) {
                                font-size: 16px;
                            }

                            @media (min-width: $tablet-small) {
                                font-size: 18px;
                            }

                            @media (min-width: $tablet-large) {
                                font-size: 21px;
                            }

                            @media (min-width: $desktop-small) {
                                font-size: 21px;
                            }

                            @media (min-width: $desktop-large) {
                                font-size: 21px;
                            }
                        }
                    }
                }
            }
        }
    }

    .contact {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: 100vh;
        padding: 0 0;

        @media (min-width: $mobile) {
            padding: 100px $page-padding-x-m;
            height: initial;
        }

        @media (min-width: $tablet-small) {
            padding: 0 0;
            height: 100vh;

            @media (max-height: 1000px) {
                height: 150vh;
            }
        }

        @media (min-width: $tablet-large) {
            padding: 0 0;
            height: 100vh;

            @media (max-height: 1000px) {
                height: 150vh;
            }
        }

        @media (min-width: $desktop-small) {
            padding: 0 0;
            height: 100vh;

            @media (max-height: 1000px) {
                height: 150vh;
            }
        }

        @media (min-width: $desktop-large) {
            padding: 0 0;
            height: 100vh;

            @media (max-height: 1000px) {
                height: 150vh;
            }
        }

        h1,
        h2 {
            font-family: 'Roboto';
            font-style: normal;
            font-weight: 700;
            font-size: 56px;
            line-height: 140%;

            display: flex;
            align-items: center;
            text-align: center;

            @media (min-width: $mobile) {
                font-size: 24px;
            }

            @media (min-width: $tablet-small) {
                font-size: 36px;
            }

            @media (min-width: $tablet-large) {
                font-size: 48px;
            }

            @media (min-width: $desktop-small) {
                font-size: 56px;
            }

            @media (min-width: $desktop-large) {
                font-size: 56px;
            }
        }

        h2 {
            margin-bottom: 80px;
        }
    }
}