@keyframes headerFadeIn {
    from {
        top: -120px;
    }

    to {
        top: 0;
    }
}

@keyframes introZoom {
    from {
        mask-size: 1%;
    }

    to {
        mask-size: 2000%;
    }
}

@keyframes introExpandLine {
    from {
        width: 0%
    }

    to {
        width: 80%
    }
}

@keyframes fadeIn {
    from {
        opacity: 0
    }

    to {
        opacity: 1
    }
}

@keyframes slideIn {
    from {
        opacity: 0;
        transform: translateY(100px);
    }

    to {
        opacity: 1;
        transform: translateY(0)
    }
}

@keyframes slideOut {
    from {
        opacity: 1;
        transform: translateY(0)
    }

    to {
        opacity: 0;
        transform: translateY(-100px);
    }
}

.slideIn {
    animation: slideIn 1s forwards;
}

.slideOut {
    animation: slideOut 1s forwards;
}

@keyframes introHeadingFade {
    from {
        bottom: -100px
    }

    to {
        bottom: 0
    }
}

@keyframes introSubheadingFade {
    from {
        top: -50px
    }

    to {
        top: 0
    }
}

@keyframes getInTouchFade {
    from {
        left: -50px
    }

    to {
        left: 0px
    }
}

